import React from 'react'
import './_Nav.scss'
import { useSelector, useDispatch } from 'react-redux';
import { setQuery, setSearch } from '../../redux/searchBarReducer/searchBarActions'
import { resetSearch } from '../../redux/catalogueReducer/catalogueActions'
import AppBar from '@material-ui/core/AppBar';
import PetsIcon from '@material-ui/icons/Pets';
import IconButton from '@material-ui/core/IconButton';
import Toolbar from '@material-ui/core/Toolbar';
import MenuIcon from '@material-ui/icons/Menu';
import SearchBar from '../SearchBar/SearchBar';

export default function Nav() {
    const status = useSelector((state) => state.searchBarReducer.status);
    const dispatch = useDispatch()

    function handleClick(){
        dispatch(setQuery('')) 
        dispatch(setSearch(false))
        dispatch(resetSearch())
    }

    return (
        <div className='navContainer'>
            <AppBar className='appBar'>
                <Toolbar className='toolBar'>  
                    <IconButton className='iconButton' edge='end' onClick={handleClick}>
                        <PetsIcon />
                    </IconButton>    
                    <h2 className={`typo ${status}Typo`}>
                        Doggies
                    </h2>    
                    <SearchBar /> 
                    <IconButton className='iconButton' edge='end'>
                        <MenuIcon />
                    </IconButton>
                </Toolbar>
            </AppBar>
        </div>
    )
}